import React, { ReactNode, useRef, useEffect } from 'react';
import { ReportFillNormal } from '@ali/we-design';
import preventDefaultOnClick from '../../uitls/preventDefaultOnClick';
import { RefreshTipsTextWrapper, RefreshTipsWrapper } from './styled';

interface IProps {
  offsetX: number;
  refreshTips?: string;
  tipsNode?: string | ReactNode | null;
  onChangeOffsetX: (offsetX: number) => void;
}

const RefreshTips: React.FC<IProps> = ((props: IProps) => {
  const { offsetX, refreshTips, tipsNode, onChangeOffsetX } = props;
  const tipsRef = useRef<HTMLElement | null>(null);

  useEffect(() => {
    if (!tipsRef.current) return;
    const { offsetWidth } = tipsRef.current;
    onChangeOffsetX(48 - offsetWidth / 2 - 12 * 2 + 2);
  }, [onChangeOffsetX]);

  return (
    <RefreshTipsWrapper
      ref={tipsRef}
      offsetX={Math.abs(offsetX)}
      onClick={preventDefaultOnClick}
      onMouseDown={preventDefaultOnClick}
    >
      {tipsNode || (
        <>
          <ReportFillNormal />
          <RefreshTipsTextWrapper>{refreshTips}</RefreshTipsTextWrapper>
        </>
      )}
    </RefreshTipsWrapper>
  );
});

export default RefreshTips;
